
const initParam = {
	conditions: {},
	orders: [],
	pageIndex: 0,
	isFirstLoad: true,
	pageSize: 10
}
const initialState = {
	nav: [],
	user: {},
	loading: false,
	data: [],
	count: 0,
	param: Object.assign({}, initParam),
	dialog: '',
	gritter: {},
	rspdata: {}
};

const functions = {
	initState: (state, action) => {
		return {
			...state,
			param: Object.assign({}, initParam),
			data: [],
			count: 0,
			url: '',
			rspdata: {},
			
		};
	},
	addLoading: (state, action) => {
		return {
			...state,
			loading: true
		};
	},
	removeLoading: (state, action) => {
		return {
			...state,
			loading: false
		};
	},
	addGritter: (state, action) => {
		let result = action.options;

		return {
			...state,
			gritter: {
				text: result.text,
				position: 'center'
			}
		};
	},
	removeGritter: (state, action) => {
		return {
			...state,
			gritter: {}
		};
	},
	errorGritter: (state, action) => {
		let result = action.options;
		return {
			...state,
			gritter: {
				text: result.text,
				position: 'center'
			}
		};
	},
	addDialog: (state, action) => {
		return {
			...state,
			dialog: action.options
		};
	},
	removeDialog: (state, action) => {
		return {
			...state,
			dialog: ''
		};
	},
	loadData: (state, action) => {
		return {
			...state,
			rspdata: action.result.rspdata,
			count: action.result.rspdata.count,
			data: action.result.rspdata.list,
		};

	},
	updateState: (state, action) => {
		return {
			...state,
			...action.state
		}
	}
}

function reducers(state = Object.assign({}, initialState), action) {

	switch (action.type) {
		case 'initState':
			state = functions.initState(state, action);
			break;
		case 'addLoading':
			state = functions.addLoading(state, action);
			break;
		case 'removeLoading':
			state = functions.removeLoading(state, action);
			break;
		case 'addGritter':
			state = functions.addGritter(state, action);
			break;
		case 'errorGritter':
			state = functions.errorGritter(state, action);
			break;
		case 'removeGritter':
			state = functions.removeGritter(state, action);
			break;
		case 'addDialog':
			state = functions.addDialog(state, action);
			break;
		case 'removeDialog':
			state = functions.removeDialog(state, action);
			break;
		case 'loadData':
			state = functions.loadData(state, action);
			break;
		case 'updateState':
			state = functions.updateState(state, action);
			break;
		default:
			break;
	}

	return state;
}

export {
	reducers
}


